*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
}
.head{
  display: flex;
  justify-content: space-between;
  height: 4rem;
  align-items: center;
  width: 80%;
  position: sticky;
}
.head :nth-child(1){
  display: flex;
  gap: 10px;
  align-items: center;
}
.head:nth-child(1) span{
  font-weight: 600;
  font-size: 30px;
}
nav{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
nav button {
  border: none;
  color: white;
  cursor: pointer;
  font-weight: 700;
  font-size: 15px;
  color: rgb(33, 33, 55);
  background-color: transparent;
}

.getstart_btn{
   border: none;
   width: none;
}
.getstart_btn>button{
  padding: 10px 10px;

  background-color: rgb(46, 46, 171);
  color: rgb(255, 255, 255);
  box-sizing:10px;
  font-weight: 600;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  text-align: center;

}
.root{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

header{
  top: 0px;
  left: 0px;
  position: sticky;
  width: 100%;
}

.sect1{
  background-image: url('https://images.unsplash.com/photo-1604014237800-1c9102c219da?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80');
  height: 500px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
}
.subcontainer{
  width: 400px;
  margin: 8rem;
  text-align: center;
}
.subcontainer>div{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 2rem;;
}
.subcontainer>div button{
  padding:.5rem 1.5rem;
}
button{
  border-radius: 10px;;
  border: none;
  box-shadow: 2px 2px 2px rgba(0, 0, 0);
  cursor: pointer;
}
.heading{
  color: #E11D48;
  text-align: center;
}
.subcontainer>h2{
 font-size: 3rem;
}
.subcontainer>p{
  font-size: 1rem;
  font-family: 'Times New Roman', Times, serif;
  /* color: rgb(63, 45, 16); */
}
.sect2{
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
  justify-content: center;
  align-items: center;
  margin: 3rem;
  gap: 5rem;
  height:100%;
  width:10%;
}
.sect2 .info_sec{
  margin: 1rem;
  }
.sect2 h2{
  font-size: 2.5rem;
  margin-top: 15px;
}
.info_sec button{
  border-radius: 10px;;
  border: none;
  box-shadow: 2px 2px 2px rgba(0, 0, 0);
  cursor: pointer;
  margin-top: 20px;
  padding: 20px 40px;
}
.img_sec img{
  border-radius: 10px;
  height: 700px;
  width: 800px;
  margin: 2rem;
}
.info_sec{
  background-color: antiquewhite;
  padding: 5rem 5rem;
  border-radius:10px;
  margin: 2rem;
  height: 50%;
}
.details{
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.details img{
  border-radius: 50%;
}
.parent> p{
  width: 200px;
  padding: 10px;
  background-color: antiquewhite;
  border-radius: 10px;
  margin: 20px;
  font-size: larger;
  font-family: 'Times New Roman', Times, serif;
}
.sec3{
  display: flex;
  flex-wrap: wrap;
}
.foot{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 5rem;
  background-color: antiquewhite;
  width: 100%;
}
.foot ul{
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 30px;
}
.foot p{
  width: 400px;
}
a{
  padding: 10px;
  font-weight: bold;
}
a:hover{
  color: blue;
  cursor: pointer;

}
button:hover{
  background-color: blanchedalmond;
  color: black;
 
}